import { Float32ItemArray } from "./FixSizeArray";
class PointFactory {
    createInstance(index, typedArray) {
        return new PointImpl(typedArray, index);
    }
}
/**
 * Point 
 * TypedArray
 * PointListRemove
 */
class PointImpl {
    constructor(array, index) {
        this.array = array;
        this.index = index;
    }
    get x() {
        return this.array[2 * this.index];
    }
    set x(val) {
        this.array[2 * this.index] = val;
    }
    get y() {
        return this.array[2 * this.index + 1];
    }
    set y(val) {
        this.array[2 * this.index + 1] = val;
    }
}
/**
 * 
 */
export class PointList extends Float32ItemArray {
    constructor(size) {
        super(2, size, new PointFactory());
    }
    push(x = 0, y = 0) {
        if (this.len + 1 === this.max) {
            throw new Error("Array index out of range.");
        }
        this.list[this.len].x = x;
        this.list[this.len].y = y;
        this.len = (this.len + 1) | 0;
        return this.list[((this.len - 1) | 0)];
    }
    set(index, x, y) {
        if (index > this.len + 1) {
            throw new Error("Array index out of range.");
        }
        this.list[index].x = x;
        this.list[index].y = y;
        if (this.len === index) {
            this.len = index;
        }
        return this.list[index];
    }
}



